
import {useState, useEffect, useRef, useMemo} from "react";

//checkbox
import { TreeSelect } from 'primereact/treeselect';
import { NodeService } from './service/NodeService';
import './styles/TreeSelectDemo.css';


//data table
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { ProductService } from './service/ProductService';
import { Toast } from 'primereact/toast';
import './styles/DataTableDemo.css';

 //charts:


//fantype select
import { Dropdown } from 'primereact/dropdown';
import './styles/DropdownDemo.css';


//airflow input
import { InputNumber } from 'primereact/inputnumber';
import './styles/airflow.css'


//airflow tolerance
import { Slider } from 'primereact/slider';
import { InputText } from 'primereact/inputtext';
import './styles/SliderDemo.css';

//image
import { Image } from 'primereact/image';

//button submit form
import { Button } from 'primereact/button';
import CanvasJSReact from "./charts/canvasjs.react";


function App() {

    //for checkboxs
    const [nodes, setNodes] = useState(null);
    const [selectedNodeKeys2, setSelectedNodeKeys2] = useState(null);
    const nodeService = new NodeService();



    useEffect(() => {
        nodeService.getTreeNodes().then(data => setNodes(data));
    }, []); // eslint-disable-line react-hooks/exhaustive-deps


    //for datatable
    const [products, setProducts] = useState([]);
    const [selectedProduct1, setSelectedProduct1] = useState(null);

    useEffect(() => {
        const productService = new ProductService();
        productService.getProductsAll().then(data => {

            setProducts(data);
        });
    },[]);

    //toast service
    const toast = useRef(null);
    const showWarn = () => {
        toast.current.show({severity:'warn', summary: 'Fan Type', detail:' Please select a fan type from the menu on the left ( Lutfen fan seçimi yapınız) ', life: 3500});
    }

    //fan options
    const [fanOptions, setFanOptions] = useState([]);
    const [loading, setLoading] = useState(false);

    //fan specifications table
    const [fan_specs, setFanSpecs] = useState([]);



    // //for dropdown fan type
    // const [selectedCity1, setSelectedCity1] = useState(null);
    // const cities = [
    //     { name: 'New York', code: 'NY' },
    //     { name: 'Rome', code: 'RM' },
    //     { name: 'London', code: 'LDN' },
    //     { name: 'Istanbul', code: 'IST' },
    //     { name: 'Paris', code: 'PRS' }
    // ]
    // const onCityChange = (e) => {
    //     setSelectedCity1(e.value);
    // }


    //airflowinput
    const [airFlow, setAirFlow] = useState(5000);


    //airflow tolerance
    const [airflowTolerance, setAirflowTolerance] = useState(10);
    const [airflowToleranceBelow, setAirflowToleranceBelow] = useState(10);


    //pressureinput
    const [pressure, setPressure] = useState(0);

    //Pressure tolerance
    const [pressureTolerance, setPressureTolerance] = useState(10);
    const [pressureToleranceBelow, setPressureToleranceBelow] = useState(10);


    //handle form submittion and fetch data

    let handleSubmit = async (e) => {



        if( (selectedNodeKeys2 == null) || (Object.keys(selectedNodeKeys2).length === 0)) {
            e.preventDefault();
            showWarn();
            return false;
        }


        e.preventDefault();
        const productService =  new ProductService()
        productService.getProducts(selectedNodeKeys2, airFlow,
            airflowTolerance, airflowToleranceBelow, pressure,
            pressureTolerance,pressureToleranceBelow

        ).then(data => {

                setProducts(data);

            }

        );
    };


    //handle selection change
    const [airflowOptions, setAirflowOptions] = useState([]);
    const [pressureOptions, setPressureOptions] = useState([]);

    const handleSelection = (e) => {

        setSelectedProduct1(e.value);

    };

    useMemo(() => {


        if(selectedProduct1 != null) {


            setFanOptions(selectedProduct1['fan_options']);
            setFanSpecs([selectedProduct1]);

            let airflowoptions = [];
            let pressureoptions = [];
            selectedProduct1['fan_options'].forEach( item => {
                airflowoptions.push({ y: item.fields.air_flow , label: item.fields.air_flow})
                pressureoptions.push({ y: item.fields.pressure, label: item.fields.pressure})
            });

            setAirflowOptions(airflowoptions);
            setPressureOptions(pressureoptions);

        }

    }, [selectedProduct1]);


    // chart:
    var CanvasJS = CanvasJSReact.CanvasJS;
    var CanvasJSChart = CanvasJSReact.CanvasJSChart;

  //  chart options :
    		const options = {
				animationEnabled: true,
				title:{
					text: "Performance Chart"
				},
				axisY : {
					title: "Airflow(mt3/h)"
				},
				toolTip: {
					shared: true
				},
				data: [{
					type: "spline",
					name: "Airflow",
					showInLegend: true,
					dataPoints: airflowOptions
				},
				{
					type: "spline",
					name: "Pressure",
					showInLegend: true,
					dataPoints: pressureOptions
				}]
		}


  return (
    <div className="App">

        <Toast ref={toast} />


        <div className="flex  m-2 p-2 text-base font-bold  align-items-center text-blue-600">
           UVENTS FAN SELECTION
        </div>

        <div className="grid border-1 border-blue-300 flex justify-content-center  p-1">
            <div className="col flex  border-1 border-blue-100 flex-column overflow-auto">

                <form className="flex flex-column  w-12" onSubmit={handleSubmit} >

                    {/*tree select fans checkbox*/}
                    {/*treeselect-demo removed from className */}
                    <div className="m-2">
                        <div className="card">

                        <TreeSelect value={selectedNodeKeys2} options={nodes} onChange={(e) => setSelectedNodeKeys2(e.value)} display="chip" selectionMode="checkbox" placeholder="Select Items"></TreeSelect>

                        </div>

                    </div>


                    <h6 className="m-2">Air Flow</h6>
                    <div className="card">
                        <div className="col-12 ">
                            <div className="p-inputgroup">
                                <InputNumber value={airFlow} onValueChange={(e) => setAirFlow(e.value) } mode="decimal" placeholder="air flow" useGrouping={false} />
                                <span className="p-inputgroup-addon">mt/3</span>
                            </div>
                        </div>
                    </div>



                    {/* air flow tolerance   */}
                    {/*slider-demo  removed from className*/}
                    <h6 className="m-2">Air Flow Tolerance</h6>
                    <div className="m-2 flex flex-row">
                        <div className="card m-1">
                            <h7>Above % :</h7>
                            <InputText value={airflowTolerance} onChange={(e) => setAirflowTolerance(e.target.value)} />
                            <Slider value={airflowTolerance} onChange={(e) => setAirflowTolerance(e.value)} />
                        </div>
                        <div className="card m-1 ">
                            <h7>below % :</h7>
                            <InputText value={airflowToleranceBelow} onChange={(e) => setAirflowToleranceBelow(e.target.value)} />
                            <Slider value={airflowToleranceBelow} onChange={(e) => setAirflowToleranceBelow(e.value)} />
                        </div>
                    </div>


                    {/*  Pressure  */}
                    <h6 className="m-2">Pressure</h6>
                    <div className="card">
                        <div className="col-12 ">
                            <div className="p-inputgroup">
                                <InputNumber value={pressure} onValueChange={(e) => setPressure(e.value)}  mode="decimal" placeholder="Pressure" useGrouping={false} />
                                <span className="p-inputgroup-addon">Pa</span>
                            </div>
                        </div>
                    </div>

                    {/* Pressure tolerance   */}
                    {/*slider-demo  removed from className*/}
                    <h6 className="m-2">Pressure Tolerance</h6>
                    <div className="m-2 flex flex-row">
                        <div className="card m-1">
                            <h7>Above % :</h7>
                            <InputText value={pressureTolerance} onChange={(e) => setPressureTolerance(e.target.value)} />
                            <Slider value={pressureTolerance} onChange={(e) => setPressureTolerance(e.value)} />
                        </div>
                        <div className="card m-1 ">
                            <h7>below % :</h7>
                            <InputText value={pressureToleranceBelow} onChange={(e) => setPressureToleranceBelow(e.target.value)} />
                            <Slider value={pressureToleranceBelow} onChange={(e) => setPressureToleranceBelow(e.value)} />
                        </div>
                    </div>

                    {/*submit form*/}
                    <Button type="submit" label="Submit" className="mt-2" />


                </form>




            </div>

            <div className="col-9 border-1 border-blue-100 overflow-auto ml-1">

                {/*data table*/}
                <div className="datatable-selection-demo">

                    <Toast ref={toast} />
                    <div className="card">
                        <DataTable value={products} scrollable scrollHeight="500px" selectionMode="single" selection={selectedProduct1} onSelectionChange={ (e) => handleSelection(e)} dataKey="pk" responsiveLayout="scroll">
                            <Column field="fields.fan_code" header="Code" sortable></Column>
                            <Column field="fan_type" header="Type" sortable></Column>
                            <Column field="fields.max_airflow" header="Max-AirFlow" sortable></Column>
                            <Column field="fields.max_pressure" header="Max-Pressure" sortable></Column>
                            <Column field="fields.power" header="Power" sortable></Column>

                        </DataTable>
                    </div>

                </div>



            </div>

        </div>
        <div className="grid border-1 border-blue-300 flex justify-content-center  flex-row p-1">
            <div className="col flex  align-items-center  justify-content-center border-1 border-blue-100 ">

                <div className="card w-10">
                    <h5>Preview</h5>

                    			<CanvasJSChart options = {options}
				                /* onRef={ref => this.chart = ref} */
                                />
                </div>
            </div>

            <div className="col flex border-1 border-blue-100 m-1 flex-column align-items-center justify-content-center overflow-auto  ">

                {/*  Fan Options  */}

                <div className="card w-10">
                    <h5>Fan Specifications</h5>
                    <DataTable value={fan_specs} scrollable scrollHeight="100px"  responsiveLayout="scroll" >
                        <Column field="fields.voltage" header="Voltage(V)" ></Column>
                        <Column field="fields.frequency" header="Frequency(Hz)" ></Column>
                        <Column field="fields.power" header="Power(W)" ></Column>
                        <Column field="fields.current" header="Current(A)" ></Column>
                        <Column field="fields.rpm" header="Rpm(d/d)" ></Column>
                        <Column field="fields.ip_code" header="IP" ></Column>
                        <Column field="fields.weigth" header="Weigth(kg)" ></Column>


                    </DataTable>
                </div>

                <div className="card w-10">
                    <h5>Fan Variations</h5>
                    <DataTable value={fanOptions} scrollable scrollHeight="200px"  responsiveLayout="scroll" >
                        <Column field="fields.air_flow" header="Airflow" ></Column>
                        <Column field="fields.pressure" header="Pressure" ></Column>

                    </DataTable>
                </div>

            </div>

        </div>

    </div>
  );
}

export default App;
