
import axios from "axios";

export class ProductService {


    getProductsAll() {
        // return [{ a: 'b'}];
        return axios.get('/api/').then(res => res.data).catch(err => err.response)
    }

    getProducts(selectedNodeKeys2, airFlow,
            airflowTolerance, airflowToleranceBelow, pressure,
            pressureTolerance,pressureToleranceBelow) {

        const config = {

            keys: selectedNodeKeys2,
            air_flow: airFlow,
            air_flow_tolerance_above: airflowTolerance,
            air_flow_tolerance_below: airflowToleranceBelow,
            pressure: pressure,
            pressure_tolerance_above: pressureTolerance,
            pressure_tolerance_below: pressureToleranceBelow

        }

        console.log(config);

        return axios.post('/api/fans/', config).then(res => res.data).catch(err => err.response)
        // return [{a : 'b'} ]

    }


}